<template>
	<div id="single-blog">
		<div class="operation">
			<router-link :to="'/EditBlog/' + this.id">修改</router-link>
			<button @click="deleteBlog()">删除</button>
		</div>
		<h2>{{blog.title}}</h2>
		<p>{{blog.content}}</p>
	</div>
</template>


<script>
import axios from 'axios'
	export default {
		name: "single-blog",
		data(){
			return {
				id: this.$route.params.id,
				blog: {}
			}
		},
		methods: {
			deleteBlog(){
				axios.delete(`https:\/\/vue-blog-87ce0.firebaseio.com/blogs/${this.id}.json`)
					.then((result) => {
						this.$router.push({path: "/"});
					})
			}
		},
		created(){
			// this.$http.get('https://vue-blog-87ce0.firebaseio.com/blogs/'+ this.id + '.json')
			axios.get('https://vue-blog-87ce0.firebaseio.com/blogs/'+ this.id + '.json')
			.then(data => {
				this.blog = data.data;;		
			})
			
		}
	}

</script>
<style scoped>
#single-blog {
	max-width: 500px;
	margin: 10px auto;	
	padding: 5px;
	background-color: #eee;
	box-sizing: border-box;
}

.operation{
	float: right;
}
</style>




